import { Component, OnInit, OnDestroy } from '@angular/core';

//import
import { ActivatedRoute, Router } from '@angular/router';
import { BlogService } from '../blog.service';
import { BlogHttpService } from '../blog-http.service';
import { error } from 'protractor';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit, OnDestroy {


  constructor(private _route: ActivatedRoute, private router: Router, private blogservice: BlogService,private bloghttp:BlogHttpService) { }

  public allblogs;

  ngOnInit() {
    // this.allblogs = this.blogservice.getAllblogs();
    // this.allblogs=this.bloghttp.getAllblogs();
    this.allblogs=this.bloghttp.getAllblogs().subscribe(
      data=>{
        console.log(data);
        this.allblogs=data["data"];
      },
      error=>{
        console.log(error.errormessage);
      }
    )
  }

  ngOnDestroy(): void {

  }

}
