import { Component, OnInit } from '@angular/core';

import { ActivatedRoute, Router } from '@angular/router';
import { BlogHttpService } from '../blog-http.service';
import { ToastrService } from 'ngx-toastr';


@Component({
  selector: 'app-blog-edit',
  templateUrl: './blog-edit.component.html',
  styleUrls: ['./blog-edit.component.css']
})
export class BlogEditComponent implements OnInit {

  public editBlog;
  public cats: String[] = ["a", 'b', "Techonology", "Action"];

  constructor(private toastr:ToastrService ,private activatedRoute: ActivatedRoute, private router: Router, private blogHttpService: BlogHttpService) { }

  ngOnInit() {
    // console.log(this.activatedRoute);
    let editId = this.activatedRoute.snapshot.paramMap.get("blogid");
    this.blogHttpService.getCurrele(editId).subscribe(
      data => {
        this.editBlog = data['data'];
      }
    )
  }

  
  public editedBlog(){
    this.blogHttpService.editedBlog(this.editBlog.blogId, this.editBlog).subscribe(
      data=>{
        this.toastr.success('blog Edited successfully', 'Success!');
        setTimeout(() => {
          this.router.navigate(['/blog', this.editBlog.blogId]);
        }, 500);
      },
      error=>{
        alert(error.errormessage);
      }
    )
  }

}
