import { Component, OnInit, OnDestroy } from '@angular/core';

//import
import { ActivatedRoute, Router } from '@angular/router';
import { BlogService } from '../blog.service';
import { BlogHttpService } from '../blog-http.service';
import { error } from 'protractor';
import { Location } from '@angular/common';


@Component({
  selector: 'app-blog-view',
  templateUrl: './blog-view.component.html',
  styleUrls: ['./blog-view.component.css'],
  providers: [Location]
})


export class BlogViewComponent implements OnInit, OnDestroy {
  
  public currblog;

  constructor(private location:Location ,private _route:ActivatedRoute, private router:Router,private bloghttp:BlogHttpService, private blogservice:BlogService) { 
  }

  ngOnInit() {

    let currblogid=this._route.snapshot.paramMap.get("blogid");
    console.log("currblogid "+currblogid);
    this.currblog=this.bloghttp.getCurrele(currblogid);
    this.currblog.subscribe(
      data=>{
        this.currblog=data["data"];
        console.log(this.currblog);

      },
      error=>{
        console.log(error.errormessage);
      }
    )
    console.log(this.currblog);
  }

  public deleteBlog(){
    this.bloghttp.deleteBlog(this.currblog.blogId).subscribe(
      data=>{
        setTimeout(() => {
          this.router.navigate(['/home']);
        }, 500);
      },
      error=>{
        alert(error.errormessage);
      }
    )
  }

  public goprev(){
    this.location.back();
  }

  ngOnDestroy(): void {
  }


}
