import { Component, OnInit } from '@angular/core';
import { BlogHttpService } from '../blog-http.service';
import { error } from 'protractor';
import { ActivatedRoute, Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';


@Component({
  selector: 'app-blog-create',
  templateUrl: './blog-create.component.html',
  styleUrls: ['./blog-create.component.css']
})
export class BlogCreateComponent implements OnInit {

  public blogTitle: string;
  public blogdesc: string;
  public blogbodyhtml: string;
  public blogcat: string;

  constructor(private toastr: ToastrService, private bloghttpservice: BlogHttpService, private activatedRoute: ActivatedRoute, private router: Router) { }

  public cats: String[] = ["a", 'b', "Techonology", "Action"];
  ngOnInit() {
  }

  public createBlog(): any {
    let blogdata = {
      title: this.blogTitle,
      description: this.blogdesc,
      blogBody: this.blogbodyhtml,
      category: this.blogcat
    }
    // console.log(blogdata);

    this.bloghttpservice.createBlog(blogdata).subscribe(
      data => {
        this.toastr.success('blog created successfully', 'Success!');
        // console.log(data.data.blogId);
        setTimeout(() => {
          this.router.navigate(['/blog', data.data.blogId]);
        }, 200);
      },
      error => {
        this.toastr.success(`Couldn't able to create the blog`, 'Error!');
      }
    );
  }

}
