import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class BlogService {

  public allblogs=[
    {
    "blogid":"1",
    "tags":[],
    "title":"Blog Title 1",
    "description":"description for blog 1",
    "created":"2018-10-10T12:20:48.854Z"
    },
    {
      "blogid":"2",
      "tags":["tag1","tag2"],
      "title":"Blog Title 2",
      "description":"description for blog 2",
      "created":"2018-10-10T12:20:48.854Z"
      },
      {
        "blogid":"3",
        "tags":[],
        "title":"Blog Title 3",
        "description":"description for blog 3",
        "created":"2018-10-10T12:20:48.854Z"
        }
  ]

  public currblog;

  constructor() { 

  }


  //return all the blogs available
  public getAllblogs():any {
    return this.allblogs;
  }
  

  //get current blog resp to id
  public getCurrele(id):any{

    for(let blog of this.allblogs)
    {
          if(id==blog.blogid)
          {
              this.currblog=blog;
          }
    }
    return this.currblog;
  }

}
